.App {
  margin: 0;
  height: 100%;
}

/* header */

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  height: auto;
  border-bottom: 1px solid rgb(238, 238, 238);
  padding: 0 20px 0 100px;
  background-color: #fff;
}

.header-text {
  font-size: 2em;
  font-weight: normal;
  text-align: center;
}

.menu-button {
  position: absolute;
  left: 30px;
  background-image: url('data:image/svg+xml;base64,');
  height: 43px;
  width: 43px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

/* Sidebar */

.sidebar {
  -ms-flex-preferred-size: 260px;
      flex-basis: 260px;
  overflow-y: scroll;
}

.search-engine {
  position: relative;
}

.label-search {
  font-style: italic;
  position: absolute;
  color: #65B0E1;
  top: 15px;
  left: 20px;
  z-index: 1;
  font-size: 0.9em;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.input-search {
  font-size: 0.95em;
  color: #555;
  border: none;
  width: 100%;
  padding: 25px 0 20px 20px;
  height: 50px;
  -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.5);
          box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.5);
}

.input-search:focus+.label-search, .input-search:valid+.label-search {
  top: 0px;
  left: 0px;
  color: #fff;
}

.list-el {
  padding: 22px 22px 22px 22px;
  color: #333;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  font-size: 1em;
  border-bottom: 1.1px solid rgb(219, 219, 219);
}

.list-el:hover {
  color: #65B0E1;
  font-size: 1.4em;
}

.list-el:focus {
  background-color: #65B0E1;
  color: #fff;
}

/* map */

.map {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
}

.info-window-title {
  padding-left: 10px;
  font-size: 1.5em;
}

.info-window {
margin: 0 0 0 10px;
text-align: center;
font-family: 'Hind Siliguri', sans-serif;
}

.info-window-details {
  font-size: 1.2em;
  font-weight: bold;
  padding: 0 0 10px 10px;
}

.info-window-icon {
  background-image: url('data:image/svg+xml;base64,');
  background-repeat: no-repeat;
  background-position: center;
  margin: 10px 0 10px 0;
  height: 64px;
}

/* footer */

.footer {
  position: absolute;
  bottom: 20px;
  right: 25%;
}

.copyrights {
  color: #555;
  font-size: 0.9em;
}

/* error */

.error-modal {
  display: -ms-flexbox;
  display: flex;
  height: calc(100vh - 160px);
  background-color: #fff;
}

.api-failed {
  height: calc(100vh - 160px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.8em;
}

.api-failed-message {
  padding: 6px 21px;
  color: #555
}

@media only screen and (max-width: 850px) {
  .App {
    margin: 0;
    height: 100vh;
  }
  .header-text {
    font-size: 1.8em;
  }
  .menu-button {
    top: 15px;
  }

  .copyrights {
    color: #555;
    padding: 4px 4px 4px 19px;
  }
  .footer {
    right: 0;
    bottom: 0;
  }
  .api-failed {
    min-height: calc(100vh - 160px);
    font-size: 1.5em;
  }

  .api-failed-message {
  color: #555
}
}

@media only screen and (max-width: 700px) {

  .header-text {
    font-size: 1.5em;
  }

  .error-modal {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .map {
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-order: -1;
        order: -1;
  }

  .copyrights {
    font-size: 0.72em;
  }

  .info-window-title {
    font-size: 1.3em;
  }

  .info-window-icon {
    display: none;
  }

  .info-window-details {
    font-size: 1.1em;
  }
}


